import React, { useState,useEffect } from 'react';
import Structure from '../../Structure';
import Body from './Body';
import SEO from '@modules/SEO';
import mixpanel from '@components/mixpanel';


export default function CompanyLandingPage() {

    useEffect(() => {
        //mixpanel
        mixpanel.track('Sojiare Company Page', {
            'Opted out of email': true,
            'ip': '0',
        });
    }, []);


    return (
        <>
            <SEO
                title="Sojiare-Verify what is Original or Suspicious"
                description="In a world of distrust, build trust and credibility with sojiare"
            />
            <Structure fullpage company>
                <Body />
            </Structure>
        </>
    );
}
